* {
  margin: 0;
  box-sizing: border-box;
}

body {
  width: 45%;
  margin: 90px auto;
  background: linear-gradient(to right, rgb(165, 165, 100), rgb(100, 153, 124));
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 75vh;
  background: rgba(0, 0, 0, 0.575);
  border-radius: 20px;
}

.App-header {
  background-color: rgba(0, 0, 0, 0.432);
  overflow: visible;
  width: 100%;
  height: 75px;
  border-radius: 20px 20px 0 0;
  display: flex;
  justify-content: left;
  align-items: center;
}

.img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: 0 30px;
}

h1 {
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5em;
}

.Messages-list {
  padding: 20px 0;
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
  list-style: none;
  padding-left: 0;
  flex-grow: 1;
  overflow: auto;
}

.Messages-message {
  display: flex;
  margin-top: 10px;
}

.Messages-message.currentMember {
  /*justify-content: flex-end;*/
  flex-direction: row-reverse;
  text-align: right;
}

.Message-content {
  display: inline-block;
}

.currentMember > .Message-content {
  align-items: flex-end;
}

.Messages-message > .avatar {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  display: inline-block;
  margin: 0 10px -10px;
}


.Message-content > .text {
  padding: 10px;
  max-width: 400px;
  margin: 0;
  border-radius: 12px;
  background-color: cornflowerblue;
  color: white;
  display: inline-block;
}

.currentMember > .Message-content .text {
  background-color: orangered;
}



form {
  display: flex;
  width: 100%;
  justify-content: space-between;
  max-width: 900px;
  background-color:rgba(0, 0, 0, 0.836);
  border-radius: 0 0 20px 20px;
  height: 75px;
  align-items: center;
}

input {
  padding: 5px;
  font-size: 16px;
  color: white;
  border: none;
  border-radius: 0 0 0 20px;
  background-color:rgba(0, 0, 0, 0);
  flex-grow: 1;
}

input:focus-visible {
  outline: -webkit-focus-ring-color auto 0;
}

button {
  margin: 5px;
  font-size: 15px;
  font-weight: 700;
  background-color: rgb(95, 170, 95);
  color: white;
  border: none;
  width: 100px;
  height: 40px;
  border-radius: 15px;
  margin-left: 10px;
  
}